footer {
  width: 100%;
  padding: 44px 0;
  min-width: 320px;
  order: 3;
  -webkit-order: 3;
  background-color: #3c3c3c;
}
.footer-wrapp {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer-logo {
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
  color: #fff;
}
.footer-text {
  color: #fff;
  margin: 0 0 0 60px;
}
